
import { defineComponent } from "vue";
import VueDatePicker from "@vuepic/vue-datepicker";
import "@vuepic/vue-datepicker/dist/main.css";
import { FormValidation } from "@/shared/services/FormValidationService";
import { PFM20300141_BODY, PFM20300141_RES, } from "@/shared/type/PFM20300141.type";
import { FeeInfoByMethodTypeCode, PFM20100113_REQ_BODY, PFM20100113_RES, } from "@/shared/type/PFM20100113.type";
import { PFM20300121_BODY, PFM20300121_RES, } from "@/shared/type/PFM20300121.type";
import { PFM20300131_BODY, PFM20300131_RES, } from "@/shared/type/PFM20300131.type";
import { PFM20300112_BODY, PFM20300112_RES, FEE_BY_AMOUNT_RANGE_DETAIL, MODIFY_CARD, } from "@/shared/type/PFM20300112.type";
import { modalController } from "@ionic/vue";
import CurrencyInput from '@/shared/components/CurrencyInput/CurrencyInput.vue'
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';

const formValidation = new FormValidation();

enum FeeCalcApplyBaseCode {
    amountFee = '01',
    rateFee = '02',
}

export default defineComponent({
    name: "pfm203001sc01",
    components: {
        VueDatePicker,
        CurrencyInput,
        ModifyCard,
        BreadCrumb,
        SelectOption,
    },

    data() {
        const feeAmount = {
            applySeqNo: '',
            productFeeCode: "",
            feeApplyStatusCode: "",
            feeCurrencyCode: "",
            feeCalcApplyBaseCode: "01",
            applyStartDate: new Date(),
            applyEndDate: new Date(),
            rangeEndAmount: "0",
            rangeStartAmount: "0",
            baseFeeAmount: "0",
            baseFeeRate: "0",
            feeMaxAmount: "0",
            feeMinAmount: "0",
            maxFeeRate: "0",
            minFeeRate: "0",
        };

        const feeAmountDate = {} as FEE_BY_AMOUNT_RANGE_DETAIL;
        const productFeeCodeList = [] as FeeInfoByMethodTypeCode[];
        const selectProductFeeCode = {
            feeName: "",
            applyStartDate: "",
            applyEndDate: "",
            feeDescription: "",
            productFeeCode: "",
            feeCurrencyCode: "",
            feeShortName: "",
            applySeqNo: "",
            feeCalcApplyBaseCode: "",
        } as FeeInfoByMethodTypeCode;

        const modifyCard = {
            registerTellerId: "",
            registerTellerName: "",
            registerDate: "",
            registerTime: "",
            updatedTellerId: "",
            updatedTellerName: "",
            updatedDate: "",
            updatedTime: "",
            deleteTellerId: "",
            deleteTellerName: "",
            deleteDate: "",
            deleteTime: "",
        } as MODIFY_CARD;

        return {
            productFeeCodeEdit: '',
            feeName: "",
            feeAmount,
            feeAmountDate,
            productFeeCodeList,
            selectProductFeeCode,
            modifyCard,
            showDisabled: false,
            showDefault: false,
            isInvalid: false,
            disableKey: false,
            applyEndDate: "",
            applyStartDate: "",
            feeCurrencyCode: "",
            newProductFeeCode: "",
            minDate: "",
            maxDate: "",
            isInvalidMinMaxBaseAmount: false,
            isInvalidMinMaxBaseRate: false,
            isInvalidBaseFeeRate: false as boolean,
            isDisableFeeCalcBaseCode: false as boolean,
            isInvalidFeeCalcBaseCode: false as boolean,
            isDisableBaseFeeRate: false as boolean,
            isDisableBaseFeeAmount: false as boolean,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
            productFee: {
                calcCode: '',
                startDate: new Date(),
                endDate: new Date()
            },
        };
    },

    async created() {
        if (this.approvalType === '') {
            await this.getProductFeeCodeList();
            if (this.queryRoute.type === "edit") {
                this.inputUpdate();
                await this.getFeeAmountRangeDetail();
            } else {
                this.inputRegister();
            }
        } else {
            await this.getProductFeeCodeList();

            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body

                this.selectProductFeeCode.productFeeCode = authDetailParsed.productFeeCode
                this.selectProductFeeCode.feeCurrencyCode = authDetailParsed.feeCurrencyCode
                this.newProductFeeCode = authDetailParsed.productFeeCode

                if (this.productFeeCodeList.length > 0) {
                    const tempFee = this.productFeeCodeList.find((v: FeeInfoByMethodTypeCode) => v.productFeeCode == authDetailParsed.productFeeCode && v.feeCurrencyCode == authDetailParsed.feeCurrencyCode) as FeeInfoByMethodTypeCode
                    if (tempFee) {
                        this.productFeeCodeEdit = `${tempFee.productFeeCode} : ${tempFee.feeName}` || ''
                    }
                }

                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const body: PFM20300112_BODY = {
                        productFeeCode: String(authDetailParsed.productFeeCode),
                        feeCurrencyCode: String(authDetailParsed.feeCurrencyCode),
                        applyStartDate: String(authDetailParsed.applyStartDate),
                        rangeStartAmount: Number(authDetailParsed.rangeStartAmount),
                        rangeEndAmount: Number(authDetailParsed.rangeEndAmount),
                        applySeqNo: Number(authDetailParsed.applySeqNo),
                    };

                    const res = (await this.$request.getRequest("PFM20300112", body, false)) as PFM20300112_RES;

                    this.feeAmount = {
                        applySeqNo: res.body.applySeqNo,
                        feeApplyStatusCode: res.body.feeApplyStatusCode,
                        rangeEndAmount: res.body.rangeEndAmount,
                        rangeStartAmount: res.body.rangeStartAmount,
                        maxFeeRate: res.body.maxFeeRate,
                        minFeeRate: res.body.minFeeRate,
                        feeMaxAmount: res.body.feeMaxAmount,
                        feeMinAmount: res.body.feeMinAmount,
                        feeCurrencyCode: res.body.feeCurrencyCode,
                        baseFeeRate: res.body.baseFeeRate,
                        baseFeeAmount: res.body.baseFeeAmount,
                        applyStartDate: this.$datetime.convertStringToDate(res.body.applyStartDate),
                        applyEndDate: this.$datetime.convertStringToDate(res.body.applyEndDate),
                        productFeeCode: res.body.productFeeCode,
                        feeCalcApplyBaseCode: res.body.feeCalcApplyBaseCode
                    };
                } else {
                    this.feeAmount = {
                        applySeqNo: authDetailParsed.applySeqNo,
                        feeApplyStatusCode: authDetailParsed.feeApplyStatusCode,
                        rangeEndAmount: authDetailParsed.rangeEndAmount,
                        rangeStartAmount: authDetailParsed.rangeStartAmount,
                        maxFeeRate: authDetailParsed.maxFeeRate,
                        minFeeRate: authDetailParsed.minFeeRate,
                        feeMaxAmount: authDetailParsed.feeMaxAmount,
                        feeMinAmount: authDetailParsed.feeMinAmount,
                        feeCurrencyCode: authDetailParsed.feeCurrencyCode,
                        baseFeeRate: authDetailParsed.baseFeeRate,
                        baseFeeAmount: authDetailParsed.baseFeeAmount,
                        applyStartDate: this.$datetime.convertStringToDate(authDetailParsed.applyStartDate),
                        applyEndDate: this.$datetime.convertStringToDate(authDetailParsed.applyEndDate),
                        productFeeCode: authDetailParsed.productFeeCode,
                        feeCalcApplyBaseCode: authDetailParsed.feeCalcApplyBaseCode
                    };
                }
            })
        }
    },

    computed: {
        queryRoute() {
            return this.$route.query;
        },
        displayName(): string {
            if (this.queryRoute.type === "edit") {
                return this.showDisabled
                    ? this.$t("pfm203001sc01.label.fee_by_amount_edit")
                    : this.$t("pfm203001sc01.label.fee_by_amount_edit");
            }
            return this.$t("pfm203001sc01.label.fee_by_amount_register");
        },
        formattedStartDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeAmount.applyStartDate)
        },
        formattedEndDate(): string {
            return this.$datetime.formatVueDatePicker(this.feeAmount.applyEndDate)
        },
        validateRangeAmount(): boolean {
            return Number(this.feeAmount.rangeStartAmount) > Number(this.feeAmount.rangeEndAmount);
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        isDisableRate(): boolean {
            return this.productFee.calcCode !== FeeCalcApplyBaseCode.rateFee
        },
        isDisableAmount(): boolean {
            return this.productFee.calcCode !== FeeCalcApplyBaseCode.amountFee
        },
        isInvalidFieldFeeCalcBaseCode(): boolean {
            return this.feeAmount.feeCalcApplyBaseCode === '' ? true : false;
        },
        invalidFeeMinAmountByFeeCalc(): boolean {
            return Number(this.feeAmount.feeMinAmount) > Number(this.feeAmount.feeMaxAmount) ? true : false;
        }
    },

    watch: {
        selectProductFeeCode: {
            handler(newFeeInfo) {
                this.feeCurrencyCode = newFeeInfo.feeCurrencyCode;
                this.newProductFeeCode = newFeeInfo.productFeeCode;
                this.productFee.startDate = this.$datetime.convertStringToDate(newFeeInfo.applyStartDate)
                this.productFee.endDate = this.$datetime.convertStringToDate(newFeeInfo.applyEndDate)
                this.productFee.calcCode = newFeeInfo.feeCalcApplyBaseCode
                if (this.$route.query.type == 'register') {
                    this.feeAmount.applyStartDate = this.$datetime.convertStringToDate(newFeeInfo.applyStartDate)
                    this.feeAmount.applyEndDate = this.$datetime.convertStringToDate(newFeeInfo.applyEndDate)
                }
                if (this.$route.query.type !== "edit") {
                    this.productFeeCodeEdit = `${newFeeInfo.productFeeCode} : ${newFeeInfo.feeName}` || ''
                }
            },
            deep: true,
        },

        feeAmount: {
            handler(newfeeAmount) {
                if (newfeeAmount.feeCalcApplyBaseCode === '02') {
                    if (newfeeAmount.feeMinAmount > newfeeAmount.feeMaxAmount) {
                        this.isInvalid = true;
                    } else {
                        this.isInvalid = false;
                    }
                    this.isDisableFeeCalcBaseCode = false;
                    this.isDisableBaseFeeRate = false;
                    this.isDisableBaseFeeAmount = true;
                    this.feeAmount.baseFeeAmount = '0';
                } else {
                    this.isDisableFeeCalcBaseCode = true;
                    this.isDisableBaseFeeRate = true;
                    this.isDisableBaseFeeAmount = false;
                    this.feeAmount.baseFeeRate = '0';
                    newfeeAmount.feeMaxAmount = newfeeAmount.baseFeeAmount;
                    newfeeAmount.feeMinAmount = newfeeAmount.baseFeeAmount;
                }

            },
            deep: true,
        },

        'feeAmount.applyEndDate'(value) {
            if (value && formValidation.startDateEndDateValidation(this.feeAmount.applyStartDate, this.feeAmount.applyEndDate) == true) {
                this.isInvalid = false;
            }
        },

        'feeAmount.applyStartDate'(value) {
            if (value && formValidation.startDateEndDateValidation(this.feeAmount.applyStartDate, this.feeAmount.applyEndDate) == true) {
                this.isInvalid = true;
            }
        },

        'feeAmount.productFeeCode'(value: string) {
            this.isInvalid = formValidation.validateWhiteSpace(value);
        },
    },

    methods: {
        inputRegister() {
            this.showDefault = true;
            this.showDisabled = false;
            this.disableKey = true;
        },

        inputUpdate() {
            this.showDefault = false;
            this.showDisabled = true;
            this.disableKey = true;
        },

        handleEdit() {
            this.inputRegister();
            this.disableKey = false;
        },

        async getProductFeeCodeList() {
            const body: PFM20100113_REQ_BODY = {
                feeCalcMethodTypeCode: "02",
            };

            const res = (await this.$request.getRequest("PFM20100113", body, false)) as PFM20100113_RES;

            if (res.header.result) {
                this.productFeeCodeList = res.body.list;
            }
        },

        async getFeeAmountRangeDetail() {
            const body: PFM20300112_BODY = {
                productFeeCode: String(this.queryRoute.productFeeCode),
                feeCurrencyCode: String(this.queryRoute.feeCurrencyCode),
                applyStartDate: String(this.queryRoute.applyStartDate),
                rangeStartAmount: Number(this.queryRoute.rangeStartAmount),
                rangeEndAmount: Number(this.queryRoute.rangeEndAmount),
                applySeqNo: Number(this.queryRoute.applySeqNo),
            };

            const res = (await this.$request.getRequest("PFM20300112", body, false)) as PFM20300112_RES;

            if (res.header.result) {
                const feeAmountDetail = JSON.parse(JSON.stringify(res.body));

                this.feeAmountDate = JSON.parse(JSON.stringify(res.body));

                this.selectProductFeeCode.productFeeCode = String(this.queryRoute.productFeeCode);
                this.selectProductFeeCode.feeCurrencyCode = String(this.queryRoute.feeCurrencyCode);

                if (this.productFeeCodeList.length > 0) {
                    const tempFee = this.productFeeCodeList.find((v: FeeInfoByMethodTypeCode) => v.productFeeCode == res.body.productFeeCode && v.feeCurrencyCode == res.body.feeCurrencyCode) as FeeInfoByMethodTypeCode
                    if (tempFee) {
                        this.productFeeCodeEdit = `${tempFee.productFeeCode} : ${tempFee.feeName}` || ''
                    }
                }

                this.feeAmount = {
                    feeApplyStatusCode: feeAmountDetail.feeApplyStatusCode,
                    rangeEndAmount: feeAmountDetail.rangeEndAmount,
                    rangeStartAmount: feeAmountDetail.rangeStartAmount,
                    maxFeeRate: feeAmountDetail.maxFeeRate,
                    minFeeRate: feeAmountDetail.minFeeRate,
                    feeMaxAmount: feeAmountDetail.feeMaxAmount,
                    feeMinAmount: feeAmountDetail.feeMinAmount,
                    feeCurrencyCode: String(this.queryRoute.feeCurrencyCode),
                    baseFeeRate: feeAmountDetail.baseFeeRate,
                    baseFeeAmount: feeAmountDetail.baseFeeAmount,
                    applyStartDate: this.$datetime.convertStringToDate(feeAmountDetail.applyStartDate),
                    applyEndDate: this.$datetime.convertStringToDate(feeAmountDetail.applyEndDate),
                    productFeeCode: String(this.queryRoute.productFeeCode),
                    applySeqNo: feeAmountDetail.applySeqNo,
                    feeCalcApplyBaseCode: feeAmountDetail.feeCalcApplyBaseCode
                };

                this.modifyCard = {
                    registerTellerId: String(this.feeAmountDate.firstRegisterTellerID),
                    registerTellerName: String(this.feeAmountDate.firstRegisterTellerName),
                    registerDate: String(this.feeAmountDate.firstRegisterDate),
                    registerTime: String(this.feeAmountDate.firstRegisterTime),
                    updatedTellerId: this.feeAmount.feeApplyStatusCode === '04' ? "" : res.body.lastChangeTellerID,
                    updatedTellerName: this.feeAmount.feeApplyStatusCode === '04' ? "" : res.body.lastChangeTellerName,
                    updatedDate: this.feeAmount.feeApplyStatusCode === '04' ? "" : res.body.lastChangeDate,
                    updatedTime: this.feeAmount.feeApplyStatusCode === '04' ? "" : res.body.lastChangeTime,
                    deleteTellerId: this.feeAmount.feeApplyStatusCode === '04' ? res.body.lastChangeTellerID : "",
                    deleteTellerName: this.feeAmount.feeApplyStatusCode === '04' ? res.body.lastChangeTellerName : "",
                    deleteDate: this.feeAmount.feeApplyStatusCode === '04' ? res.body.lastChangeDate : "",
                    deleteTime: this.feeAmount.feeApplyStatusCode === '04' ? res.body.lastChangeTime : "",
                };
            }
        },

        validateAmountAndRate(min: number, base: number, max: number): boolean {
            let result: boolean;
            if (min > base || base > max) {
                result = false;
            } else {
                result = true;
            }
            return result;
        },

        handleSave() {
            this.isInvalid = false;

            if (
                this.selectProductFeeCode.productFeeCode === "" ||
                this.feeAmount.applyStartDate.toString() === "Invalid Date" && this.feeAmount.applyEndDate.toString() === "Invalid Date" ||
                !formValidation.startDateEndDateValidation(this.feeAmount.applyStartDate, this.feeAmount.applyEndDate) ||
                Number(this.feeAmount.feeMinAmount) > Number(this.feeAmount.feeMaxAmount) ||
                !formValidation.minMaxValueValidation(Number(this.feeAmount.rangeStartAmount), Number(this.feeAmount.rangeEndAmount))) {
                this.isInvalid = true;
                return;
            } else {
                this.applyStartDate = this.$datetime.convertDateToString(
                    new Date(this.feeAmount.applyStartDate)
                );
                this.applyEndDate = this.$datetime.convertDateToString(
                    new Date(this.feeAmount.applyEndDate)
                );
            }

            this.$dialog.onOpen({
                content: `${this.$t("message.would_you_like_to_save")}`,
                dialogMode: "Question",
                callback: () => {
                    if (this.queryRoute.type === "edit") {
                        this.updateFeeAmountRange().then((res: boolean) => {
                            if (res) {
                                modalController.dismiss();
                                this.getFeeAmountRangeDetail();
                                this.inputUpdate();
                            }
                        });
                    } else {
                        this.registerFeeAmountRange().then((res: boolean) => {
                            if (res) {
                                modalController.dismiss();
                                this.goToList();
                            }
                        });
                    }
                    modalController.dismiss();
                },
            });
        },

        async updateFeeAmountRange() {
            const body: PFM20300131_BODY = {
                productFeeCode: this.selectProductFeeCode.productFeeCode,
                feeCurrencyCode: this.selectProductFeeCode.feeCurrencyCode,
                applyStartDate: this.applyStartDate,
                applyEndDate: this.applyEndDate,
                rangeEndAmount: this.feeAmount.rangeEndAmount !== "" ? Number(this.feeAmount.rangeEndAmount) : 0,
                rangeStartAmount: this.feeAmount.rangeStartAmount !== "" ? Number(this.feeAmount.rangeStartAmount) : 0,
                baseFeeAmount: this.feeAmount.baseFeeAmount !== "" ? Number(this.feeAmount.baseFeeAmount) : 0,
                baseFeeRate: this.feeAmount.baseFeeRate !== "" ? Number(this.feeAmount.baseFeeRate) : 0,
                feeMaxAmount: this.feeAmount.feeMaxAmount !== "" ? Number(this.feeAmount.feeMaxAmount) : 0,
                feeMinAmount: this.feeAmount.feeMinAmount !== "" ? Number(this.feeAmount.feeMinAmount) : 0,
                maxFeeRate: this.feeAmount.maxFeeRate !== "" ? Number(this.feeAmount.maxFeeRate) : 0,
                minFeeRate: this.feeAmount.minFeeRate !== "" ? Number(this.feeAmount.minFeeRate) : 0,
                applySeqNo: Number(this.feeAmount.applySeqNo),
                feeApplyStatusCode: this.feeAmount.feeApplyStatusCode,
                feeCalcApplyBaseCode: this.feeAmount.feeCalcApplyBaseCode
            };

            const res = (await this.$request.getRequest("PFM20300131", body, false)) as PFM20300131_RES;

            if (res.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: "Success",
                    callback: () => {
                        this.getFeeAmountRangeDetail()
                        modalController.dismiss();
                    },
                });
            }

            return res.header.result;
        },

        async registerFeeAmountRange() {
            const body: PFM20300121_BODY = {
                productFeeCode: this.selectProductFeeCode.productFeeCode,
                feeCurrencyCode: this.selectProductFeeCode.feeCurrencyCode,
                applyStartDate: this.applyStartDate,
                applyEndDate: this.applyEndDate,
                rangeEndAmount: this.feeAmount.rangeEndAmount !== "" ? Number(this.feeAmount.rangeEndAmount) : 0,
                rangeStartAmount: this.feeAmount.rangeStartAmount !== "" ? Number(this.feeAmount.rangeStartAmount) : 0,
                baseFeeAmount: this.feeAmount.baseFeeAmount !== "" ? Number(this.feeAmount.baseFeeAmount) : 0,
                baseFeeRate: this.feeAmount.baseFeeRate !== "" ? Number(this.feeAmount.baseFeeRate) : 0,
                feeMaxAmount: this.feeAmount.feeMaxAmount !== "" ? Number(this.feeAmount.feeMaxAmount) : 0,
                feeMinAmount: this.feeAmount.feeMinAmount !== "" ? Number(this.feeAmount.feeMinAmount) : 0,
                maxFeeRate: this.feeAmount.maxFeeRate !== "" ? Number(this.feeAmount.maxFeeRate) : 0,
                minFeeRate: this.feeAmount.minFeeRate !== "" ? Number(this.feeAmount.minFeeRate) : 0,
                feeCalcApplyBaseCode: this.feeAmount.feeCalcApplyBaseCode
            };

            const res = (await this.$request.getRequest("PFM20300121", body, false)) as PFM20300121_RES;

            if (res.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            }

            return res.header.result;
        },

        handleDelete() {
            this.$dialog.onOpen({
                content: `${this.$t("message.are_you_sure_you_want_to_delete")}`,
                dialogMode: "Question",
                callback: () => {
                    this.deleteData();
                    modalController.dismiss();
                },
            });
        },

        async deleteData() {
            const body: PFM20300141_BODY = {
                productFeeCode: String(this.queryRoute.productFeeCode),
                feeCurrencyCode: String(this.queryRoute.feeCurrencyCode),
                applyStartDate: String(this.queryRoute.applyStartDate),
                rangeStartAmount: Number(this.queryRoute.rangeStartAmount),
                rangeEndAmount: Number(this.queryRoute.rangeEndAmount),
                applySeqNo: Number(this.queryRoute.applySeqNo),
            };

            const res = (await this.$request.getRequest("PFM20300141", body, false)) as PFM20300141_RES;

            if (res) {
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                        this.$router.push("/PFM203001SC00");
                    },
                });

                this.$router.back();
            }
        },

        goToList() {
            this.$router.push("PFM203001SC00");
        },

        handleCancel() {
            this.$dialog.onOpen({
                content: `${this.queryRoute.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                dialogMode: "Question",
                callback: () => {
                    modalController.dismiss();
                    if (this.queryRoute.type === "edit") {
                        this.inputUpdate();
                        this.getFeeAmountRangeDetail();
                    } else {
                        this.goToList();
                    }
                },
            });
        },
    },
});
